@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Pacifico);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#282c34}.app{margin:0;min-width:100%}.dashboard{display:block;margin:100px 0 30px;padding:50px;border-radius:45px;border-top:none;border-bottom:none;border-left:solid #282c34 25px;border-right:solid #282c34 25px;background-color:ghostwhite;min-height:80vh}.item{padding:50px}.header{display:block;position:fixed;overflow:hidden;top:0;right:0;left:0;background-color:#282c34;text-align:center;z-index:999}.header .header-title{margin:0;color:yellow;font-size:36px;line-height:100px;vertical-align:middle;font-family:Pacifico, serif}.main-footer{display:block;position:fixed;bottom:0;left:0;right:0;background-color:#282c34;text-align:center;z-index:999}.main-footer .copy-right{font-size:12px;color:yellow}.main-footer .copy-right .copy-right-link{line-height:30px;vertical-align:middle;padding:0 5px;font-weight:bold;text-decoration:none;color:inherit}.searchView{padding:100px 0}.searchbar{position:absolute;top:100px;left:25px;width:calc(100% - 250px);padding:0 100px;min-height:100px;max-height:100px;margin:0;background-color:rgba(40,44,3,0.1);border-radius:25px;line-height:100px;vertical-align:middle;font-size:36px;border:none;outline:none}.backButton{position:absolute;top:113px;left:40px;z-index:9999;background-color:transparent;border:none;padding:12px 16px;font-size:36px;color:#282c34;cursor:pointer}.backButton:hover{color:gray}.searchButton{position:fixed;bottom:60px;right:50px;z-index:9999;background-color:transparent;border:none;padding:12px 16px;font-size:36px;color:#282c34;cursor:pointer}.searchButton:hover{color:gray}

